.nvt-slider {
  position: relative;
}

.nvt-track {
  overflow-x: hidden;
}

.nvt-track::-webkit-scrollbar {
  display: none;
}

.nvt-list {
  display: flex;
  align-items: center;
  transition: transform 0.4s linear;
}

.nvt-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.nvt-dots {
  display: flex;
  align-items: center;
}

.nvt-dot {
  width: 8px;
  height: 8px;
  margin: 0 5px;
  background-color: currentColor;
  opacity: 0.3;
  border-radius: 100%;
  cursor: pointer;
  transition: all 0.4s linear;
}

.nvt-dot.--active {
  opacity: 1;
  width: 12px;
  height: 12px;
}

.nvt-arrow {
  display: inline-block;
  color: currentColor;
  cursor: pointer;
}

.nvt-arrow.--disabled {
  opacity: 0.3;
  pointer-events: none;
}

